 
 .header-inicial{
  background: #355C7D;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #C06C84, #6C5B7B, #355C7D);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #C06C84, #6C5B7B, #355C7D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
 }
 .landing-grid{
  background: #355C7D;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #C06C84, #6C5B7B, #355C7D);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #C06C84, #6C5B7B, #355C7D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}
.contenido{   
background: #8A2387;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #F27121, #E94057, #8A2387); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.mdl-layout__drawer-button{
  background: transparent !important;
}
.active {
  background: #FFB233;
  font-weight: 900;
  text-transform: uppercase;   
}
  .mdl-navigation__link.active{   
  color: #2A2317 !important;
}
.logo a{
  font-family: 'Piedra', sans-serif;
  color: white;
  text-decoration: none;
}
 
 .avatar-img{
   height: 648px;
   margin-top: 20px;
   margin-left: 27%;
   position: absolute;
   z-index: 9999;
 }

.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin-top: 150px;
  border-radius: 10px;
}
.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}
.banner-text hr{
  border-top: 5px dashed white;
  width: 50%;
  margin: auto;
}
.banner-text{
  color: white;
  font-size: 20px;
  padding: 1em;
}
.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;
}
.cta{
  margin-top: 10px;
}

.btn-accion {   
  margin-left: 10px !important;
}
.btn-accion a {
  color: white;
  font-weight: 900;
  text-decoration: none;
}
/*Proyectos Css*/
.proyecto-grid{  
  display: flex;
  width: 90%;
  
}
.proyecto-grid .card{
  width: 512px;
  margin: auto;
}
.proyecto-grid .card .card-titulo{
  color: rgb(59, 47, 47);
  font-weight: bold;
  height: 200px;
  background: #2193b0;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #6dd5ed, #2193b0);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #6dd5ed, #2193b0); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
}
.mdl-tabs__tab.is-active{
  color: rgba(247, 237, 237, 0.87) !important;
}
.categoria-tabs a, .footer a{    
   text-decoration: none;    
    font-weight: 700;
}
.footer a{    
  color: white;
  margin-right: 10px;
}
.footer{
  text-align: center;
  padding: 20px;
}
.footer .ml-auto i{
  margin-right: 5px;
}
.footer .ml-auto{
  margin-top: 10px;
}

.btn{   
  font-weight: 800 !important;
  border: 1px solid grey !important;
}
.btn.cat{
  background: white !important;   
}
.btn.accion{
  background: grey !important; 
  margin-right: 15px;  
}
.btn.accion.der{
  background: red !important;   
}
.btn.accion a{
  color: white !important;
  text-decoration: none;
  font-weight: 800;   
} 

.btn.error{
  margin: 1em;
  background: #FF5733 !important;
}
.btn.error a{
  color: white !important;  
  text-decoration: none;
  font-weight: 800;  
}
 
 .contenido{  
  position: relative;
  width: auto;
  height: 700px;
  padding-top: 2em;
  justify-content: center;
}
.grid{    
  text-align: center;
  justify-content: center;
  width: 80%;
  border: 2px solid red;
  background: white;
  height: 650px;
  color:black;
  margin-bottom: 20px;
}
.contenido .grid hr{
  border-top: 3px dotted rgb(75, 71, 71);
  width: 50%;
  margin: auto;
}
.grid li{
  list-style: none;
}
.contenido-lista{
  padding-left: 5rem;
}
/*Css Pagina de Contacto*/
.item-contacto{
  font-size: 24px;
  font-family: 'Anton', sans-serif;
}
.item-contacto i{
  font-size: 32px;
  padding-right: 3rem;
}
.item-contacto a{
  text-decoration: none;
  color:rgb(15, 1, 1) !important;
}

.foto-contacto{
  border-radius: 10px;
  margin-top: 10px;
}
.text-contacto{
  width: 75%;
  margin: auto;
  padding-top: 1em;
  text-align: left;
}


/*Css acerca de*/
.imagen-acercade{
  text-align: center;
}
.grid.acercade p, .lista.servicios{    
  text-align: left;   
}
.lista.servicios i{    
  padding-right: 0.5em; 
  font-size: 24px;
}
.acercade{  
  height: 900px;   
}
 .grid.acercade{  
  height: 800px; 
    
}
.grid.acercade a{  
  color: white;
  text-decoration: none;
  font-weight: 700; 
}

.titulo-servicios{
  font-weight: 900;
}
.lista.servicios img{   
  width: 24px;
}

/*Pagina Error*/

.error-img{
  width: 90%;
  height: 90%;
  margin: auto;
}
 
.nofound-page .contenido{
  justify-content: center;
  margin: auto;
  height: 600px;
  position: fixed;  
}

/*Responsive*/
@media (max-width: 740px){
  .nav-der{
    display: none !important;
  }
  .proyecto-grid {
    padding: 0 !important;     
    width: 100% !important;
}
.proyecto-grid .card {
  width: auto !important;
   
}
.mdl-card__actions {   
  padding: 3px !important;
} 
.mdl-list{
  width: 550px !important;
  text-align: left !important;
  margin-right: 150px !important;
}
.contenido-lista {
  padding-left: 0rem !important;
}
.item-contacto i {  
  padding-right: 0.5rem !important;
  font-size: 20px !important; 
}
.item-contacto {
  font-size: 20px !important;   
}
.btn.accion {   
  margin-right: 2px !important;
}
 .landing-grid {   
  position: relative;
}
.avatar-img {
  height: 50%;   
  position: relative;
  margin-top: 0px;
  margin-left: 0%;   
  z-index: 0;
}
.banner-text {   
  width: 85%;
  margin-top: 0;  
}
.banner-text h1 {
  font-size: 50px;   
}
.social-links i {   
  font-size: 2em;   
}
.imagen-acercade{
  margin-left: -44px;
}
.imagen-acercade img{
  width: 90%;
  margin-left: 39px;
}
.mdl-tabs__tab-bar {   
  justify-content: left !important;   
  height: 36px !important; 
   
}
.mdl-layout__content.contenido{
  padding-top: 0;
}
.mdl-tabs__tab{
  font-size: 9px !important;
}
 
}
 
